<template>
  <v-container>
    <v-row>
      <v-col>
        <v-btn to="/rest-apis/save"><v-icon>mdi-plus</v-icon> New</v-btn>
        <v-simple-table>
          <template v-slot:default>
            <thead>
            <tr>
              <th class="text-left">Name</th>
              <th class="text-right" style="width: 160px;"></th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="item in items" :key="item.name">
              <td>{{ item.name }}</td>
              <td style="text-align: right">
                <v-btn style="margin-right: 10px;" x-small :to="{name: 'rest-api-methods', params: {rest_api_id: item.id}}"><v-icon>mdi-edit</v-icon> Methods</v-btn>
                <v-btn x-small :to="{path: '/rest-apis/save', query: {id: item.id}}"><v-icon>mdi-edit</v-icon> Edit</v-btn>
              </td>
            </tr>
            <tr v-if="items.length === 0">
              <td colspan="2">No record found</td>
            </tr>
            </tbody>
          </template>
          <v-overlay :value="loading">Loading...</v-overlay>
        </v-simple-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

import axios from "axios";

export default {
  data() {
    return {
      loading: false,
      items: []
    }
  },
  async mounted() {
    await this.loadItems();
  },
  methods: {
    async loadItems() {
      this.loading = true;
      try {
        let response = await axios.get('/api/rest-apis',{params: {project_id: this.$store.state.project.selected.id}});
        this.items = response.data.rest_apis;
      } catch (e) {
        console.log(e);
      } finally {
        this.loading = false;
      }
    }
  }
}
</script>
